.App {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  font-weight: 200;
  font-size: 16px;
  font-family: "Helvetica Neue",Helvetica,Arial,sans-serif;
  color: #333333;
}

strong {
  font-weight: 500;
}

img {
  max-width: 100%;
}

.jumbotron {
  padding-bottom: 20px;
  margin-bottom: 0px;
}
.jumbotron p {
  font-size: 18px;
  margin-bottom: 0px;
}

.AppInfo {
  background-color: #d9edf7;
}

.bg-info {
  font-weight: 400;
}

.SmallInfo{
  font-size: 14px;
}

#Referee {
  display: flex;
  justify-content: flex-end;
  font-size: 14px;
}

#MainPage {
  display: flex;
  flex-direction: column;
  align-items: stretch;
}

#RecommendBtn {
  margin-bottom: 5px;
}